import axios from 'axios'
import { Message } from 'element-ui' // , MessageBox
import store from '../store'

// 创建axios实例
const service = axios.create({
  // baseURL: process.env.BASE_API, // api的base_url
  baseURL: 'http://w4.8zyl.cn:9090/api/v1', // api的base_url
  timeout: 20000 // 请求超时时间
})

// respone拦截器
service.interceptors.response.use(
  response => {
    return response.data
  },
  error => {
    console.log(error)
    console.log(error.response)
    if (error && error.response) {
      store.commit('CLOSE_WAITING')
      switch (error.response.status) {
        case 400:
          if (error.response.data.error_code === 1008) {
            Message({
              message: '内容重复',
              type: 'error',
              duration: 5 * 1000
            })
          } else if (error.response.data.error_code === 1101) {
            Message({
              message: '上传格式错误',
              type: 'error',
              duration: 5 * 1000
            })
          } else {
            Message({
              message: '客户端无效',
              type: 'error',
              duration: 5 * 1000
            })
          }
          break

        case 401:
          if (error.response.data.error_code === 1003) {
            Message({
              message: '令牌过期',
              type: 'error',
              duration: 5 * 1000
            })
          } else if (error.response.data.error_code === 1002) {
            Message({
              message: '令牌过期',
              type: 'error',
              duration: 5 * 1000
            })
          } else if (error.response.data.error_code === 1007) {
            Message({
              message: '密码错误',
              type: 'error',
              duration: 5 * 1000
            })
          } else if (error.response.data.error_code === 1102) {
            Message({
              message: '用户名错误',
              type: 'error',
              duration: 5 * 1000
            })
          } else {
            Message({
              message: '未知错误',
              type: 'error',
              duration: 5 * 1000
            })
          }
          // setTimeout(() => {
          //   store.dispatch('FedLogOut').then(() => {
          //     location.reload() // 为了重新实例化vue-router对象 避免bug
          //   })
          // }, 2000)
          break

        case 403:
          Message({
            message: '权限不足',
            type: 'error',
            duration: 5 * 1000
          })
          break

        case 404:
          Message({
            message: '找不到资源',
            type: 'error',
            duration: 5 * 1000
          })
          break

        case 500:
          Message({
            message: '未知错误',
            type: 'error',
            duration: 5 * 1000
          })
          break

        default:
      }
    } else if (JSON.stringify(error).indexOf('timeout') !== -1) {
      Message({
        message: '获取数据超时',
        type: 'error',
        duration: 5 * 1000
      })
    }
    // if (JSON.stringify(error).indexOf(401) !== -1) {
    //   MessageBox.confirm(
    //     '你的令牌已失效，可以取消继续留在该页面，或者重新登录',
    //     '确定登出',
    //     {
    //       confirmButtonText: '重新登录',
    //       cancelButtonText: '取消',
    //       type: 'warning'
    //     }
    //   ).then(() => {
    //     store.dispatch('FedLogOut').then(() => {
    //       location.reload() // 为了重新实例化vue-router对象 避免bug
    //     })
    //   })
    // } else {
    //   Message({
    //     message: '服务器错误',
    //     type: 'error',
    //     duration: 5 * 1000
    //   })
    // }
    return Promise.reject(error)
  }
)

export default service



