import request from '@/utils/request'

export function test (params) {
  return request({
    url: '/api/user',
    methods: 'get',
    params
  })
}

export function detail (params) {
  return request({
    url: '/details',
    methods: 'get',
    params
  })
}

export function search (params) {
  return request({
    url: '/query',
    methods: 'get',
    params
  })
}



