<template>
  <div class="inner" style="margin-top:20px;" v-loading='loading'>
    <div v-if="hasData">
      <div class="appCon">
        <el-row>
          <el-col :xs="24" :sm="18" :md='16'>
            <div class="appView clearfix">
              <div class="appLogo">
                <img :src="version.Big_icon_url" alt="">
              </div>
              <div class="appIntro">
                <h2>{{detail_data.Name}}</h2>
                <el-row>
                  <el-col :xs="24" :sm="8" :md='8'>
                    <el-rate
                      v-model="detail_data.Rate"
                      disabled
                      text-color="#ff9900"
                      score-template="{value}">
                    </el-rate>
                  </el-col>
                  <el-col :xs="24" :sm="24" :md='24' class="font12">
                    <p>更新时间: {{this.timeToYMD(version.Datetime)}}</p>
                    <p><span>{{this.dealDownTime(version.Download_num)}}</span>次下载 | <span><!--{{detail_data.rate}}%</span>好评率 | <span>-->{{detail_data.Comments.length}}人</span>评论</p>
                  </el-col>
                  <el-col :sm="24">
                    <div class="downlo">
                      <a href="" download="诠星游戏平台">安全下载</a>
                    </div>
                  </el-col>
                </el-row>
              </div>
            </div>
          </el-col>
          <el-col :xs="24" :sm="6" :md='8'>
            <div class="info" style="margin-top:25px">
              <h3>相关信息</h3>
              <p><span>大小</span>{{this.dealApkSize(version.Apk_size)}}</p>
              <p><span>版本</span> {{version.Version}}</p>
              <!-- <p><span>要求</span>{{version.plateform[0]}}</p> -->
              <p><span>开发者</span>{{detail_data.Company_name}}</p>
            </div>
          </el-col>
        </el-row>
      </div>
      <div class="intro">
        <div class="line"></div>
        <h3 class="pl10">{{detail_data.name}} 截图</h3>
        <div class="screenshotsCon">
          <el-row>
            <el-col style="padding:3px" :xs="24" :sm="12" :md='12' v-for="(v,i) in detail_data.Img_url" :key="i">
              <div>
                <img :src="v" alt="">
              </div>
            </el-col>
          </el-row>
          <div style="height:20px;"></div>
          <p class="desc" v-for="(v,i) in detail_data.Descrip" :key="i">
            {{v}}
          </p>
        </div>
      </div>
      <div class="comments">
        <h3>网友评论</h3>
        <ul>
          <li v-for="(v,i) in detail_data.Comments" :key="i">
            <section class="font12">{{v.User}}</section>
            <el-row>
              <el-col :span="12" class="textAlignLeft">
                <el-rate
                  v-model="v.Star"
                  disabled
                  text-color="#ff9900"
                  score-template={value}>
                </el-rate>
              </el-col>
              <el-col :span="12" class="textAlignRight">
                <!-- {{this.timeToYMD(v.datetime)}} -->
                {{v.datetime}}
              </el-col>
            </el-row>
            <p>{{v.Comments}}</p>
          </li>
        </ul>
      </div>
    </div>
    <div v-if="!hasData" class="noData">
      <img src="./nogame.jpg" alt="">
      <p>
        <router-link to="/">点此返回首页</router-link>
      </p>
    </div>
  </div>
</template>
<script>
import saveAs from "file-saver"; // eslint-disable-line
import { detail } from '@/api/api'
var FileSaver = require("file-saver"); // eslint-disable-line
export default {
  data: function () {
    return {
      id: 0,
      detail_data: {},
      version: {},
      comments: [],
      hasData: true,
      loading: true
    }
  },
  created () {
    console.log(this.$route.query.id)
    this.loadData()
  },
  watch: {
    $route (to, from) {
      console.log(to)
    }
  },
  methods: {
    loadData () {
      let params = {
        ai: this.$route.query.id
      }
      try {
        detail(params).then(res => {
          console.log(res.Data[0])
          if (res.Message === '成功') {
            this.loading = false
            this.detail_data = res.Data[0]
            // this.detail_data.rateStar = parseInt(this.detail_data.Rate)
            this.version = this.detail_data.Versions[0]
            this.comments = this.detail_data.comments
          } else {
            this.hasData = false
            this.$message.error(res.Message)
            this.loading = false
          }
        })
      } catch (error) {
        this.hasData = false
        this.loading = false
        this.$message.error('获取数据失败')
      }
    },
    dealDownTime (v) {
      return v / 10000 > 0 ? Math.round(v / 10000) + '万' : v
    },
    dealApkSize (v) {
      return Math.round(v / 1000) + 'MB'
    },
    dealRate (v) {
      return Math.round(v / 100)
    },
    timeToYMD (date) {
      date = new Date(date)
      var y = date.getFullYear()
      var m = date.getMonth() + 1
      var d = date.getDate()
      return y + '年' + m + '月' + d + '日'
    }
  }
}
</script>

<style lang="scss" scoped>
.appCon {
  margin: 15px;
}
.noData{
  margin:0 auto;
  max-width: 600px;
  img{
    width: 200px;
  }
  p{
    text-align:center;
    height: 40px;
    line-height: 40px;
  }
  a{
    color: #24aa42
  }
}
.appView {
  margin: 15px;
  .appLogo {
    float: left;
    img {
      height: 160px;
    }
  }
  .appIntro {
    width: 330px;
    margin-left: 20px;
    text-align: left;
    float: left;
    .downlo {
      margin-top: 12px;
      a {
        width: 98px;
        height: 36px;
        background: #24aa42;
        color: white;
        line-height: 36px;
        text-align: center;
        display: block;
        border-radius: 4px;
        border: none;
      }
    }
    .font12 {
      p {
        color: #9b9b9b;
        line-height: 22px;
        span {
          font-size: 14px;
        }
      }
    }
    h2 {
      margin: 0;
      font-weight: normal;
      font-size: 24px;
      margin-top: 10px;
    }
  }
}

.info {
  text-align: left;
  h3 {
    font-size: 16px;
    font-weight: bold;
  }
  p {
    line-height: 32px;
    color: #666666;
    font-size: 12px;
    span {
      width: 50px;
      display: inline-block;
      color: #b5b5b5;
    }
  }
}
.review {
  height: 100%;
  text-align: left;
  background: #f7f7f7;
  padding: 20px;
  h2 {
    font-size: 14px;
    margin-bottom: 15px;
  }
  i {
    font-size: 14px;
  }
  p {
    font-size: 12px;
  }
}

@media screen and (max-width: 700px) {
  .appView {
    margin: 0;
    .appLogo {
      width: 100%;
      img {
        margin: 0 auto;
        height: 100px;
      }
    }
    .appIntro {
      border-bottom: 1px solid #f4f4f4;
      text-align: center;
      margin: 0;
      width: 100%;
      .downlo {
        margin: 10px 0;
        a {
          width: 76px;
          height: 24px;
          font-size: 12px;
          margin: 0 auto;
          line-height: 24px;
        }
      }
      h2 {
        margin: 0;
      }
    }
  }
  .info {
    margin: 10px;
    text-align: center;
  }
}
.intro {
  border-bottom: 1px solid #f4f4f4;
  .line {
    height: 2px;
    background: #f4f4f4;
  }
  h3 {
    font-size: 16px;
    text-align: left;
    font-weight: bold;
    margin-top: 30px;
  }
  .screenshotsCon {
    // display: flex;
    // align-items: center;
    // flex-wrap: wrap;
    margin: 10px 0;
    .el-col {
      img {
        width: 100%;
      }
    }
    p {
      padding: 0 10px;
      font-size: 13px;
      color: #a9a9a9;
      text-align: left;
      line-height: 20px;
      text-indent: 1em;
    }
  }
}
.comments {
  margin: 20px;
  text-align: left;
  font-size: 14px;
  h3 {
    font-size: 17px;
    margin-left: 10px;
    font-weight: bold;
    color: #333333;
    text-align: left;
  }
  li {
    padding: 20px 10px 30px 10px;
    border-bottom: 1px solid #f4f4f4;
    section {
      color: #b5b5b5;
      margin: 5px 0;
    }
    .el-row {
      margin-bottom: 10px;
    }
    .textAlignRight {
      color: #b5b5b5;
    }
    p {
      font-size: 14px;
      line-height: 22px;
      color: #676767;
    }
  }
}
</style>



